<template>
  <div>
    <div>
      <el-row class="permission-box mb20">
        <el-col :sm="12" class="item">
          <div class="content">
            <div class="menu-content">
              <div class="box-head">
                <span class="mr40">菜单列表 </span>
                <!-- <span>应用：{{ activeProduct.productName || '-' }}</span> -->
              </div>
              <div class="content">
                <el-tree
                  ref="menuTree"
                  :data="activeProduct.aggregateSet"
                  node-key="id"
                  :props="menuTreeProps"
                  :show-checkbox="true"
                  :default-expand-all="true"
                  :expand-on-click-node="false"
                  :check-strictly="isCheckStrictly"
                  :default-checked-keys="selectedMenuIds"
                  @node-click="item => activeMenu = item"
                  @check="data=>menuSelect(data,activeProduct)"
                ></el-tree>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :sm="12" class="item">
          <div class="content">
            <div class="menu-content">
              <div class="box-head">
                <span class="mr40">{{ activeMenu.name }}</span>
                <span> {{ activeMenu.name ? '-':'' }} 功能列表</span>
              </div>
              <div class="content">
                <div class="no-data" v-if="activeMenu.buttons.length<=0"><span class="no-data__span text__nodata">暂无数据</span></div>
                <div class="buttons-box" v-else>
                  <div class="buttons-box-item btn" v-for="(item,index) in activeMenu.buttons" :key="index">
                    <el-button
                      :type="item.selected ? 'primary' : 'default'"
                      @click="btnSelect(item, activeMenu)"
                    >
                      {{ item.cname }}
                    </el-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
      <div class="form-footer-btn" slot="footer">
        <el-button type="primary" @click="handleSave">保存并返回</el-button>
        <el-button @click="$emit('close', 'cancel')">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getPolicy, savePolicy } from 'cseed-frame/services/_sys'
export default {
  name: 'PermissionPolicy',
  props: {
    id: {
      type: Number,
      required: true
    },
    productId: {
      type: [Number, String],
      default: 0
    }
  },
  data () {
    return {
      radio: null,
      roleType: 1,
      isLoading: false,
      productList: [], // 应用
      // 当前选中的应用
      activeProduct: this.genActivedPorduct(),
      menuTreeProps: { label: 'name', children: 'childAggregate' },
      // 当前选中的菜单
      activeMenu: this.genActivedMenu(),
      selectedMenuIds: [], // 当前选中的菜单
      isCheckStrictly: false,
      apiServer: undefined,
      api: {}
    }
  },
  methods: {
    genActivedPorduct () {
      return { productName: '', aggregateSet: [] }
    },
    genActivedMenu () {
      return { name: '', buttons: [] }
    },
    getSelectedMenus (menus = [], selectedMenu = []) {
      menus.forEach(menu => {
        const subMenus = menu[this.menuTreeProps.children]
        if (menu.selected && (!subMenus || subMenus.length === 0)) {
          selectedMenu.push(menu)
        }
        if (subMenus) {
          this.getSelectedMenus(subMenus, selectedMenu)
        }
      })
      return selectedMenu
    },
    // 应用选中(单选)
    handleProductChange (product) {
      this.productList.forEach(p => {
        if (p.productId !== product.productId) {
          p.selected = 0
        }
      })
    },
    // 应用点击
    handleProducClick (product) {
      const { aggregateSet } = product
      this.activeProduct = product
      const selectedMenus = this.getSelectedMenus(aggregateSet)
      this.selectedMenuIds = selectedMenus.map(ele => ele.id)
      this.activeMenu = this.genActivedMenu()
    },
    subMenusSelect (menu) {
      const isSelect = menu.selected
      menu[this.menuTreeProps.children].forEach(subMenu => {
        subMenu.selected = isSelect
        this.subMenusSelect(subMenu)
      })
    },
    parentMenuSelect (menu) {
      if (menu._parentMenu) {
        const siblingMenus = menu._parentMenu[this.menuTreeProps.children]
        const isSelect = siblingMenus.some(ele => ele.selected)
        menu._parentMenu.selected = Number(isSelect)
        this.parentMenuSelect(menu._parentMenu)
      }
    },
    menuSelect (menu, menuProduct) {
      menu.selected = Number(!menu.selected)
      const allMenus = menuProduct.aggregateSet || []
      const selectedMenus = this.getSelectedMenus(allMenus)
      if (selectedMenus.length > 0) {
        menuProduct.selected = 1
      }
      if (!this.isCheckStrictly) {
        this.subMenusSelect(menu) // 子菜单选中
      }
      this.parentMenuSelect(menu) // 父菜单选中

      this.selectedMenuIds = this.$refs.menuTree.getCheckedKeys()
    },
    btnSelect (btn, btnMenu) {
      btn.selected = Number(!btn.selected)
      const allBtns = btnMenu.buttons || []
      const menuIsSelect = allBtns.some(btn => btn.selected)
      const selectIndex = this.selectedMenuIds.findIndex(ele => ele === btnMenu.id)
      // 有按钮选中时，菜单必须选中
      if (menuIsSelect && selectIndex <= -1) {
        this.selectedMenuIds.push(btnMenu.id)
      }

      this.$refs.menuTree.setCheckedKeys(this.selectedMenuIds)

      // btnMenu.selected = Number(!menuIsSelect) // 由于 menuSelect 方法会对 selected 进行取反，为了保证数据的一致性，先进行一次取反
      // 如果选中按钮所属的菜单没有选中，则选中对应的菜单
      if (!btnMenu.selected) {
        this.menuSelect(btnMenu, this.activeProduct)
      }
    },
    handleSave () {
      const productList = this.productList.slice(0, this.productList.length)// JSON.parse(JSON.stringify(this.productList))
      productList.forEach(ele => {
        this.clearCacheParentMenu(ele.aggregateSet)
      })
      this.saveData({ policyId: this.id, camPolicyAggregateList: productList })
    },
    saveData (data) {
      this.isLoading = true
      savePolicy(data).then(() => {
        // this.productList = res.data
        this.$emit('close', 'save')
      }).finally(() => {
        this.isLoading = false
      })
    },

    clearCacheParentMenu (menus) {
      menus.forEach(ele => {
        ele._parentMenu = undefined
        this.clearCacheParentMenu(ele[this.menuTreeProps.children])
      })
    },

    cacheParentMenu (menus) {
      menus.forEach(menu => {
        menu[this.menuTreeProps.children].forEach(subMenu => {
          subMenu._parentMenu = menu
          this.cacheParentMenu([subMenu])
        })
      })
    },

    fetchData () {
      this.isLoading = true
      getPolicy({
        policyAggregateIds: [this.id],
        productId: this.productId
      }).then(({ data }) => {
        (data || []).forEach(p => {
          this.cacheParentMenu(p.aggregateSet)
        })
        this.productList = data
        this.handleProductChange(data[0])
        this.handleProducClick(data[0])
        const selectProduct = this.productList.find(ele => ele.selected)
        if (selectProduct) this.handleProducClick(selectProduct)
      }).finally(() => {
        this.isLoading = false
      })
    },
    evil (fn) {
      const Fn = Function // 一个变量指向Function，防止有些前端编译工具报错
      return new Fn('return ' + fn)()
    }
  },
  created () {
    this.fetchData()
  }
}
</script>

