<template>
  <div class="pages">
    <div v-if="pageType === 'list' || pageType === 'Dialog'">
      <div class="pages-title">
        <div class="filter-box" v-if="pagesData.formItems.length && pagesData.operation.topOptions.length">
          <el-button-group class="filter-btn">
            <el-button
              v-for="(item, key) in pagesData.operation.topOptions"
              :key="`topOptions_${key}`"
              plain
              type="primary"
              @click="optionsTodo(item)"
            >
              {{ item[0] }}
            </el-button>
          </el-button-group>
          <div
            v-for="(item, key) in pagesData.formItems"
            :key="`filter_${key}`"
            class="filter-input"
          >
            <el-input
              v-model="formData[item.key]"
              :placeholder="item.placePrefix ? item.placePrefix : '请输入内容'"
            ></el-input>
          </div>
          <el-button-group>
            <el-button @click="handleSearch">查询</el-button>
            <el-button @click="handleResetSearch">重置</el-button>
          </el-button-group>
        </div>
        <template v-else>
          <div class="title-info">
            <div class="title-name">{{ pagesData.title }}</div>
            <div class="title-info" v-if="pagesData.Subtitle">{{ pagesData.Subtitle }}</div>
          </div>
          <div class="title-btn">
            <slot name="btn"></slot>
          </div>
        </template>
        <div style="clear: both;"></div>
      </div>
      <div class="pages-table">
        <c-table
          :props="pagesData.table.props"
          :data="pagesData.table.data"
          :header="pagesData.table.header"
        ></c-table>
        <div
          class="pages-pagination"
          v-if="pagesData.pagesFlag"
        >
          <span class="pages-total">共{{ pagesData.pagesTotal }}条</span>
          <el-pagination
            style="float: right"
            class="pagination"
            layout="prev, pager, next, sizes, jumper"
            :current-page="pagesData.pages.page"
            :page-size="pagesData.pages.pageSize"
            :total="pagesData.pagesTotal"
            @current-change="handleCurrentChange"
            @size-change="handleChangeSize"
          ></el-pagination>
          <div style="clear: both;"></div>
        </div>
      </div>
      <slot name="dialog"></slot>
    </div>
    <resource-content
      v-if="pageType === 'Pages'"
      :template="template"
      :title="title"
      :mode="mode"
      :id="id"
      :row="row"
      :unique-id="uniqueId"
      @upsert-end="hiddenPages('ok')"
      @go-back="hiddenPages('back')"
    ></resource-content>
    <div v-if="dialogVisible">
      <el-dialog
        :title="title"
        :visible.sync="dialogVisible"
        width="50%"
        @close="hiddenDialog"
      >
        <component
          ref="dialogForm"
          :is="dialogComponents[template] || dialogComponents['Default']"
          :mode="mode"
          :id="id"
          :row="row"
          :unique-id="uniqueId"
          @upsert-end="hiddenDialog('ok')"
          @go-back="hiddenDialog('back')"
        >
        </component>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import DialogPage from './handleResource/dialogs'
import ResourceContent from './handleResource/PageIndex'
export default {
  name: 'PagesCard',
  components: {
    ResourceContent
  },
  props: {
    pagesConfig: {
      type: Object,
      default: () => {
        return {}
      }
    },
    uniqueId: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      dialogVisible: false,
      template: '',
      mode: '',
      id: '',
      row: {},
      dialogComponents: DialogPage,
      title: '',
      pageType: 'list',
      formData: {},
      pagesData: {
        pagesFlag: true,
        formItems: [],
        operation: {
          topOptions: []
        },
        title: '',
        Subtitle: '',
        pagesTotal: 0,
        pages: {
          pageSize: 10,
          page: 1
        },
        table: {
          props: {
            border: false
          },
          data: []
        }
      }
    }
  },
  methods: {
    // 查询列表
    handleSearch () {
      this.pagesData.pages.page = 1
      this.getList()
    },
    // 重置查询条件
    handleResetSearch () {
      this.pagesData.pages.page = 1
      this.formData = {}
      this.getList()
    },
    tabOptions ({ row, option }) {
      if (option[1] === 'Dialog') {
        this.dialogVisible = true
      }
      this.pageType = option[1]
      this.title = option[0]
      this.template = option[2]
      this.mode = option[3]
      this.row = row
    },
    optionsTodo (option) {
      if (option[1] === 'Dialog') {
        this.dialogVisible = true
      }
      this.pageType = option[1]
      this.title = option[0]
      this.template = option[2]
      this.mode = option[3]
    },
    hiddenDialog (type) {
      this.dialogVisible = false
      this.pageType = 'list'
      if (type === 'ok') {
        this.getList()
      }
    },
    hiddenPages (type) {
      this.pageType = 'list'
      if (type === 'ok') {
        this.getList()
      }
    },
    handleCurrentChange (e) {
      this.pagesData.pages.page = e
      this.getList()
    },
    handleChangeSize (e) {
      this.pagesData.pages.pageSize = e
      this.getList()
    },
    getList () {
      if (this.pagesConfig.listApi) {
        this.pagesConfig.listApi({
          ...this.pagesData.params,
          ...this.pagesData.pagesFlag ? this.pagesData.pages : {},
          ...this.formData
        }).then(({ data }) => {
          this.$set(this.pagesData.table, 'data', this.pagesData.pagesFlag ? data.list : this.pagesData.getData ? this.pagesData.getData(data) : data)
          this.pagesData.pagesTotal = data.total
        })
      }
    },
    deepMerge (target = {}, source = {}) {
      target = this.deepClone(target)
      if (typeof target !== 'object' || typeof source !== 'object') return false
      for (const prop in source) {
        if (!source.hasOwnProperty(prop)) continue
        if (prop in target) {
          if (typeof target[prop] !== 'object') {
            target[prop] = source[prop]
          } else if (typeof source[prop] !== 'object') {
            target[prop] = source[prop]
          } else if (target[prop].concat && source[prop].concat) {
            target[prop] = target[prop].concat(source[prop])
          } else {
            target[prop] = this.deepMerge(target[prop], source[prop])
          }
        } else {
          target[prop] = source[prop]
        }
      }
      return target
    },
    deepClone (obj) {
      // 对常见的“非”值，直接返回原来值
      if ([null, undefined, NaN, false].includes(obj)) return obj
      if (typeof obj !== 'object' && typeof obj !== 'function') {
        // 原始类型直接返回
        return obj
      }
      const o = this.array(obj) ? [] : {}
      for (const i in obj) {
        if (obj.hasOwnProperty(i)) {
          o[i] = typeof obj[i] === 'object' ? this.deepClone(obj[i]) : obj[i]
        }
      }
      return o
    },
    array (value) {
      if (typeof Array.isArray === 'function') {
        return Array.isArray(value)
      }
      return Object.prototype.toString.call(value) === '[object Array]'
    }
  },
  created () {
    this.pagesData = this.deepMerge(this.pagesData, this.pagesConfig)
    this.getList()
  }
}
</script>
