<template>
  <div>
    <el-form
      :model="roleFormData"
      :rules="roleRules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
      label-position="top"
    >
      <el-form-item label="角色名称" prop="policyName">
        <el-input v-model="roleFormData.policyName"></el-input>
      </el-form-item>
      <el-form-item label="英文名称" prop="policyEname">
        <el-input v-model="roleFormData.policyEname"></el-input>
      </el-form-item>
      <el-form-item label="归属业务" prop="businessUniqueIds">
        <el-checkbox-group v-model="roleFormData.businessUniqueIds">
          <el-checkbox v-for="(item, index) in businessList" :label="item.uniqueId" :key="index">{{ item.name }}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="备注">
        <el-input v-model="roleFormData.remark" type="textarea" rows="4"></el-input>
      </el-form-item>
      <el-form-item>
        <div class="dialog-footer">
          <el-button type="primary" @click="submitRole">确 定</el-button>
          <el-button @click="$emit('close', 'cancel')">取 消</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

import appInfoMixins from 'cseed-frame/components/sys/appInfoMixins'
import { getBusinessList, getPolicyDetail, addRole, updateRole } from 'cseed-frame/services/_sys'
const validPassword = (rule, value, callback) => {
  const reg = /^[A-Za-z0-9]+$/
  if (!reg.test(value) && value) {
    callback(new Error('英文名称必须是数字或字母'))
  } else {
    callback()
  }
}
export default {
  mixins: [appInfoMixins],
  components: {},
  props: {
    id: {
      type: [String, Number],
      default: ''
    },
    pageType: {
      type: String,
      default: 'addRole'
    },
    roleForm: {
      type: [Object],
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      roleRules: {
        policyName: { required: true, message: '请输入角色名称', trigger: ['blur'] },
        policyEname: [
          { required: true, message: '请输入英文', trigger: ['blur'] },
          { min: 4, max: 20, message: '英文长度在 4 到 20 个字符', trigger: ['blur'] },
          { validator: validPassword, trigger: ['blur'] }
        ],
        businessUniqueIds: [
          { type: 'array', required: true, message: '请选择归属业务', trigger: 'change' }
        ]
      },
      roleFormData: {
        businessUniqueIds: []
      },
      businessList: []
    }
  },
  watch: {
    roleForm: {
      handler (newName) {
        console.log(newName)
      },
      deep: true
    }
  },
  methods: {
    submitRole () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          const formData = JSON.parse(JSON.stringify(this.roleFormData))
          const saveService = formData.id ? updateRole : addRole
          saveService({
            ...formData,
            policyCode: `${process.env.VUE_APP_PROJECT_CODE}_${this.roleFormData.policyEname}`,
            productUniqueId: this.uniqueId
          }).then(() => {
            this.$emit('close', 'save')
          })
        }
      })
    },
    evil (fn) {
      const Fn = Function // 一个变量指向Function，防止有些前端编译工具报错
      return new Fn('return ' + fn)()
    },
    getBusinessList () {
      getBusinessList({
        pageSize: 99999,
        page: 1
      }).then(res => {
        this.businessList = res.data.list
      })
    },
    getDetail () {
      getPolicyDetail({
        id: this.id
      }).then(res => {
        this.roleFormData = res.data
        this.$nextTick(() => {
          this.$refs['ruleForm'].clearValidate()
        })
      })
    }
  },
  created () {
  },
  mounted () {
    this.getBusinessList()
    if (this.id) {
      this.getDetail()
    }
  }
}
</script>

<style lang="stylus" scoped>
>>>.el-form--label-top .el-form-item__label
  padding 0
</style>
