<template>
  <div>
    <el-row class="permission-box mb20">
      <el-col :sm="24" class="item">
        <div class="content">
          <div class="tag-content">
            <div class="box-head">
              <span class="mr40">数据标签列表</span>
              <span>应用：{{ activeProduct.cname || '-' }}</span>
            </div>
            <div class="content">
              <el-tree
                ref="tagTree"
                :data="activeProduct.dataTagList"
                node-key="id"
                :props="dataTagTreeProps"
                :show-checkbox="true"
                :default-expand-all="true"
                :expand-on-click-node="false"
                :check-strictly="isCheckStrictly"
                :default-checked-keys="selectedTagIds"
                @check="data=>tagSelect(data,activeProduct)"
              ></el-tree>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <div class="form-footer-btn" slot="footer">
      <el-button type="primary" @click="handleSave">保存并返回</el-button>
      <el-button @click="$emit('close', 'cancel')">取消</el-button>
    </div>
  </div>
</template>

<script>
import { camPolicyDataTagSave, policyDataTagList } from 'cseed-frame/services/_sys'
export default {
  name: 'PermissionPolicy',
  props: {
    id: {
      type: Number,
      default: 0
    },
    productId: {
      type: [Number, String],
      default: 0
    }
  },
  data () {
    return {
      isLoading: false,
      productList: [], // 应用
      // 当前选中的应用
      activeProduct: this.genActivedPorduct(),
      dataTagTreeProps: { label: 'tagName', children: 'child' },
      selectedTagIds: [], // 当前选中的数据标签
      isCheckStrictly: false
    }
  },
  computed: {
  },
  methods: {
    genActivedPorduct () {
      return { cname: '', dataTagList: [] }
    },
    getSelectedTags (tags = [], selectedTag = []) {
      tags.forEach(tag => {
        const subTags = tag[this.dataTagTreeProps.children]
        if (tag.selected && (!subTags || subTags.length === 0)) {
          selectedTag.push(tag)
        }
        if (subTags) {
          this.getSelectedTags(subTags, selectedTag)
        }
      })
      return selectedTag
    },
    // 应用点击
    handleProducClick (product) {
      const { dataTagList } = product
      this.activeProduct = product
      const selectedTags = this.getSelectedTags(dataTagList)
      this.selectedTagIds = selectedTags.map(ele => ele.id)
    },
    subTagsSelect (tag) {
      const isSelect = tag.selected;
      (tag[this.dataTagTreeProps.children] || []).forEach(subTag => {
        subTag.selected = isSelect
        this.subTagsSelect(subTag)
      })
    },
    parentTagSelect (tag) {
      if (tag._parentTag) {
        const siblingTags = tag._parentTag[this.dataTagTreeProps.children] || []
        const isSelect = siblingTags.some(ele => ele.selected)
        tag._parentTag.selected = Number(isSelect)
        this.parentTagSelect(tag._parentTag)
      }
    },
    tagSelect (tag, tagProduct) {
      const nowTagSelected = Number(!tag.selected)
      tag.selected = nowTagSelected
      const allTags = tagProduct.dataTagList || []
      const selectedTags = this.getSelectedTags(allTags)
      if (selectedTags.length > 0) {
        tagProduct.selected = 1 // 若
      }
      if (!this.isCheckStrictly) {
        this.subTagsSelect(tag) // 子数据标签选中
      }
      this.parentTagSelect(tag) // 父数据标签选中 ！！！！！！！需要改造成单选

      this.selectedTagIds = this.$refs.tagTree.getCheckedKeys()
    },
    handleSave () {
      const productList = this.productList.slice(0, this.productList.length)// JSON.parse(JSON.stringify(this.productList))
      productList.forEach(ele => {
        this.clearCacheParentTag(ele.dataTagList)
      })
      this.saveData({ policyId: this.id, tagList: productList })
    },
    saveData (data) {
      this.isLoading = true
      camPolicyDataTagSave(data).then(() => {
        this.$emit('close', 'save')
      }).finally(() => {
        this.isLoading = false
      })
    },

    clearCacheParentTag (tags = []) {
      tags.forEach(ele => {
        ele._parentTag = undefined
        this.clearCacheParentTag(ele[this.dataTagTreeProps.children] || [])
      })
    },

    cacheParentTag (tags) {
      tags.forEach(tag => {
        (tag[this.dataTagTreeProps.children] || []).forEach(subTag => {
          subTag._parentTag = tag
          this.cacheParentTag([subTag])
        })
      })
    },

    fetchData () {
      this.isLoading = true
      policyDataTagList({
        policyAggregateIds: [this.id],
        productId: this.productId
      }).then(({ data }) => {
        (data || []).forEach(p => {
          this.cacheParentTag(p.dataTagList)
        })
        this.productList = data
        this.handleProducClick(data[0])
      }).finally(() => {
        this.isLoading = false
      })
    },
    evil (fn) {
      const Fn = Function // 一个变量指向Function，防止有些前端编译工具报错
      return new Fn('return ' + fn)()
    }
  },
  created () {
    this.fetchData()
  }
}
</script>

