<template>
  <c-pages-card :pages-config="pagesConfig" ref="pages">
    <div slot="btn">
      <el-button plain type="primary" @click="addRole">添加应用角色</el-button>
    </div>
    <el-dialog
      slot="dialog"
      :title="titleMap[pageType || 'addRole']"
      :visible.sync="dialogVisible"
      v-if="dialogVisible"
      width="800px"
      :before-close="handleClose"
    >
      <add-role-form
        v-if="['data', 'power'].indexOf(pageType) === -1"
        :page-type="pageType"
        :id="editId"
        @close="closeRole"
      ></add-role-form>
      <policy-data-tag
        :product-id="productId"
        :id="editId"
        v-if="pageType === 'data'"
        @close="closeRole"
      ></policy-data-tag>
      <permission-policy
        :product-id="productId"
        :id="editId"
        v-if="pageType === 'power'"
        @close="closeRole"
      ></permission-policy>
    </el-dialog>
  </c-pages-card>
</template>

<script>
import CPagesCard from 'cseed-frame/components/pageCard/CPagesCard.vue'
import appInfoMixins from 'cseed-frame/components/sys/appInfoMixins'
import addRoleForm from 'cseed-frame/components/sys/role/AddRoleForm.vue'
import PolicyDataTag from 'cseed-frame/components/sys/role/PolicyDataTag.vue'
import PermissionPolicy from 'cseed-frame/components/sys/role/PermissionPolicy.vue'
import { getRoleList } from 'cseed-frame/services/_sys'
export default {
  name: 'AppRole',
  mixins: [appInfoMixins],
  components: {
    CPagesCard,
    addRoleForm,
    PolicyDataTag,
    PermissionPolicy
  },
  props: {},
  data () {
    return {
      titleMap: {
        editRole: '编辑应用角色',
        addRole: '添加应用角色',
        data: '角色数据权限',
        power: '角色权限管理'
      },
      dialogVisible: false,
      pagesConfig: {
        listApi: getRoleList,
        params: {},
        title: '应用角色',
        table: {
          header: [
            {
              width: 100,
              label: '角色ID',
              prop: 'id'
            },
            {
              label: '角色名称',
              prop: 'policyName'
            },
            {
              label: '英文名称',
              prop: 'policyEname'
            },
            {
              label: '归属业务',
              prop: 'businessLineName'
            },
            {
              label: '描述',
              prop: 'remark'
            },
            {
              label: '创建时间',
              prop: 'createTime'
            },
            {
              label: '操作',
              width: 200,
              align: 'center',
              render: (h, { row }) => {
                const operation = [
                  {
                    text: '编辑',
                    handle: 'handleEdit'
                  },
                  {
                    text: '权限管理',
                    handle: 'handlePower'
                  }
                  // {
                  //   text: '数据权限',
                  //   handle: 'handleData'
                  // }
                ]
                const btnArr = []
                operation.forEach(item => {
                  btnArr.push(
                    h(
                      'el-button',
                      {
                        attrs: {
                          type: 'text'
                        },
                        on: {
                          click: () => {
                            this[item.handle](row)
                          }
                        }
                      },
                      item.text
                    )
                  )
                })
                return btnArr
              }
            }
          ],
          data: []
        }
      },
      roleForm: {},
      editId: 0,
      pageType: ''
    }
  },
  computed: {},
  methods: {
    // 权限管理编辑
    handlePower (row) {
      this.dialogVisible = true
      this.pageType = 'power'
      this.editId = row.id
    },
    // 弹窗关闭前操作
    handleClose () {
      this.pageType = ''
      this.dialogVisible = false
    },
    // 数据权限编辑
    handleData (row) {
      this.dialogVisible = true
      this.pageType = 'data'
      this.editId = row.id
    },
    // 关闭弹窗
    closeRole (type) {
      this.dialogVisible = false
      this.pageType = ''
      if (type === 'save') {
        this.$refs.pages.getList()
      }
    },
    // 角色编辑
    handleEdit (row) {
      this.pageType = 'editRole'
      this.editId = row.id
      this.roleForm = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true
    },
    // 新增角色
    addRole () {
      this.pageType = 'addRole'
      this.editId = ''
      this.roleForm = {
        businessUniqueIds: []
      }
      this.dialogVisible = true
    }
  },
  created () {
    // this.appEname = this.appInfo.ename
    this.$set(this.pagesConfig.params, 'productUniqueId', this.uniqueId)
  }
}
</script>

<style lang="stylus" scoped>
.dialog-footer
  text-align left
</style>
<style lang="stylus">
.mr40
  margin-right 40px
</style>
