
import fetch from 'cseed-frame/services/_fetch'

// 获取业务线
export function getBusinessList (params) {
  return fetch({
    method: 'get',
    url: 'auth/business/list',
    params
  })
}

// 获取角色详情
export function getPolicyDetail (params) {
  return fetch({
    method: 'get',
    url: 'auth/camPolicy/detail',
    params
  })
}

export function addRole (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicy/save',
    data
  })
}

export function updateRole (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicy/update',
    data
  })
}

export function camPolicyDataTagSave (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicyDataTag/save',
    data
  })
}

export function policyDataTagList (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicy/v2/policyDataTagList',
    data
  })
}

export function getPolicy (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicy/v2/policyDetail',
    data
  })
}

export function savePolicy (data) {
  return fetch({
    method: 'post',
    url: 'auth/camPolicy/savePolicy',
    data
  })
}

export function getRoleList (params) {
  return fetch({
    method: 'get',
    url: 'auth/camPolicy/list',
    params
  })
}

// 获取用户登录失败次数
export function getUserErrorCount (params) {
  return fetch({
    method: 'get',
    url: 'auth/user/getUserErrorCount',
    isJSON: false,
    params
  })
}

// 清除用户登录失败次数
export function clearUserErrorCount (params) {
  return fetch({
    method: 'post',
    url: 'auth/user/clearCache',
    isJSON: false,
    params
  })
}
